import React, {useState} from 'react';

const Search = ({history}) => {

  const [keyword, setKeyword] = useState('')

  const submitHandler = (e) => {
    e.preventDefault()
    if(keyword.trim()) {
      history.push(`/search/${keyword}`)
    }else {
      history.push('/')
    }
  }
  return (
    <div class='relative z-0 flex-1 px-2 flex items-center justify-center sm:absolute sm:inset-0'>
      <div class='w-full sm:max-w-xs'>
        <form onSubmit={submitHandler}>
          <div class='relative'>
            <div class='pointer-events-none absolute inset-y-0 left-0 pl-3 flex items-center'>
              <svg
                class='h-5 w-5 text-gray-400'
                xmlns='http://www.w3.org/2000/svg'
                viewBox='0 0 20 20'
                fill='currentColor'
                aria-hidden='true'
              >
                <path
                  fill-rule='evenodd'
                  d='M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z'
                  clip-rule='evenodd'
                />
              </svg>
            </div>
            <input
              id='search'
              name='search'
              onChange={(e) => setKeyword(e.target.value)}
              class='block w-full bg-white font-hind border border-gray-300 rounded-md py-2 pl-10 pr-3 text-sm placeholder-gray-500 focus:outline-none focus:text-gray-900 focus:placeholder-gray-400 focus:ring-1 focus:ring-yellow-500 focus:border-yellow-500 sm:text-sm'
              placeholder='Rechercher'
              type='search'
            />
          </div>
        </form>
      </div>
    </div>
  );
};

export default Search;
